html {
  height: 95%;
  font-size: calc(4px + 1vw);
}

body {
  overflow: hidden;
  background-color: black;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  height: 100%;
}

#root {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.screen-parent {
  display: flex;
  flex-direction: column;

  /* margin:10pt; */
  text-align: center;
  /* border: 1px solid green; */
  height: 100%;
}

.tb {
  display: flex;
  flex-direction: row;
  flex: 1;
  /* border: 1px solid orange; */
  font-size: 16pt;
  font-weight: bold;
}

.tbm {
  align-self: center;
  padding-left: 5px;
  text-align: left;
  flex: 8;
  /* border: 1px solid blue; */
}

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
  .tbm {
    font-size: 20pt;
  }
}

.tbr {
  align-self: center;
  flex: 2;
  justify-content: flex-end;
  /* border: 1px solid red; */
}

.row-container {
  flex: 10;

  display: flex;
  flex-direction: column;
  /* border: 1px solid red; */
}

.row-container > .row {
  flex: 1 1 0;
}

.row {
  display: flex;
  flex: 1 1;
  flex-direction: row;
  /* border:3px solid red; */
}

.row > .box {
  flex: 1 1;
}

.box {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5px;
  /* border: 1px solid yellow; */
}

.labelable {
  display: flex;
  flex: 1 0;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  width: 100%;
  height: 100%;
  border-radius: 8px;
  border-width: 1px;
  border-style: solid;
  border-color: pink;
  /* border:3px solid pink; */
}

.label {
  /* border: 3px solid blue; */
  /* display: flex; */
  padding: 1%;
  justify-content: center;
  align-items: center;
  max-width: 95%;
}

.widget-label {
  flex: 1;
  font-size: 12pt;
  padding-top: 3px;
  font-weight: bold;
}

.context-label {
  flex: 1;
  /* font-size: 10pt; */
}

.other-widget {
  /* border:3px solid blue; */
  /* display: flex; */
  flex: 10 1;
  padding: 2px 1% 4px 1%;
  justify-content: center;
  align-items: center;
  width: 90%;
  text-align: center;
  /* max-height: 10px; */
}

.box > button {
  height: 80%;
  width: 95%;
  font-size: 11pt;
}

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
  .box > button {
    font-size: 11pt;
  }

  .widget-label {
    font-size: 14pt;
  }

  .context-label {
    /* font-size: 10pt; */
  }
}

.box-selected {
  box-shadow: inset 0px 0px 0px 2px red;
}

.section-container > .tile > .row > .box > button {
  height: 45px;
  width: 95%;
  font-size: 14pt;
}

.selected {
  filter: brightness(100%);
}

.fil0 {
  fill: #006aad;
}
.fil1 {
  fill: white;
}

.slider-value-label {
  font-weight: bold;
  position: relative;
  top: -3px;
}

.horizontal-arrowed-list-container {
  display: flex;
  flex-direction: row;
  font-size: 11pt;
  font-weight: bold;
  /* position: relative; */
  /* top: 25%; */
}

.vertical-arrowed-list-container {
  display: flex;
  flex-direction: column;
  font-size: 11pt;
  font-weight: bold;
  position: relative;
}

.arrow-left,
.arrow-right,
.arrow-up,
.arrow-down {
  flex: 1 0;
  font-weight: normal;
  margin: auto;
}

.arrowed-list-item {
  flex: 3 0;
  align-items: center;
}
